.header {
	background-color: #eee;
	width: 100%;
	max-height: 145px;
	height: 100%;
	border-top: 5px solid $supared;
	&__container {
		display: flex;
		justify-content: space-between;
		width: 100%;
		max-width: 1000px;
		margin: auto;
		padding: 5px 10px;
		align-items: center; }
	&__title {
		min-width: 265px;
		text-align: center;
		font-size: 16px;
		font-weight: bold;
		color: black;
		margin: auto;
		padding: 0 5px; }
	&__title-link {
		color: black;
		text-decoration: none; } }

.header__title-span {
	color: $supared;
	font-size: 18px;
	// +r(480)
 }	// 	display: none

.logo {
	margin: auto;
	width: 100%;
	height: 100%;
	max-width: 100px;
	max-height: 100px;
	align-items: center;
	&__image {
		width: 100%;
		height: 100%;
		max-width: 100px;
		max-height: 100px; }
	&__link {
		width: 100%;
		height: 100%;
		max-width: 100px;
		max-height: 100px;
		border-radius: 100px;
		overflow: hidden;
		transition: all 600ms;
		&:hover {
			box-shadow: 0 0 10px $supared;
			transition: all 600ms; } } }

.infobar {
	width: 100%;
	border-bottom: 1px solid darkgray;
	&__container {
		max-width: 1000px;
		margin: auto;
		display: flex;
		justify-content: space-around;
		height: 25px; }
	&__item {
		width: 33%;
		text-align: center;
		margin: auto;
		font-size: 16px;
		font-weight: 600; }
	&__email {
		text-decoration: none;
		color: black;
		&:hover {
			text-decoration: underline;
			color: $supared; } } }


.lang {
	width: 44px;
	height: 25px;
	background: red;
	&__to-ua {
		background: url(../img/lang/ua.svg) no-repeat center; }
	&__to-en {
		background: url(../img/lang/uk1.svg) no-repeat center; } }
